<template>
  <!-- Clicking this component toggles between text and input -->
  <div class="edit-container" @click="edit = true">
    <p id="description-title">DESCRIPTION:</p>
    <!-- Input is toggled on click, updates text on enter -->
    <input type="text"
      v-if="edit"
      v-focus
      :value="description"
      @input="$emit('input', $event.target.value)"
      @keydown.enter="edit = false; description = $event.target.value"
    />
    <!-- Displays this div when not in edit mode -->
    <div class="no-focus" v-show="!edit">
      <p>{{description}}</p>
      <i class="material-icons" id="edit-icon">edit</i>
    </div>
  </div>
</template>

<script>
  export default {
  props: ['value'],
  directives: {
    focus: {
      inserted (el) {
        el.focus()
      }
    }
  },
  data () {
  return {
      edit: false,
      description: this.value
    }
  },
}
</script>

<style media="screen">
input {
  padding: 0.6em 1.2em;
  width: 100%;
  text-align: center;
  border: none;
  font-size: 16px;
}
#description-title {
  font-weight: bold;
  padding-bottom: 1.2em;
}
#edit-icon {
  padding-top: 0.6em;
  padding-left: calc(100% - 2.4em);
}
</style>
