<template>
  <div class="table-container">

    <div class="filters-container">
      <div class="filters-text-container">
        <i class="material-icons">filter_list</i>
        <p id="filter-title">filters</p>
      </div>

      <!-- FILTERS -->
      <div class="filter-btns">
        <button
          type="button"
          name="filter-btn"
          v-for="(btn, index) in sortButtons"
          @click="toggleSort(index) || sortBy(btn.filterName)"
          :class="{active: btn.isActive}"
        >
          {{btn.filterName}}
        </button>
      </div>
    </div>

    <!-- CARDS : loops through payments array and displays corresponding data -->
    <div class="card" v-for="(payment, index) in payments">
      <div class="card-title">
        <i class="material-icons">person_pin</i>
        <p>{{payment.Name}}</p>
      </div>
      <div id="card-body">
        <p>ID: {{payment.ID}}</p>
        <p>DATE: {{payment.Date}}</p>
      </div>
      <!-- Div for grouping; so 'space-between' works properly-->
      <div class="block-container">
        <div id="description-container">
          <ClickToEdit :value="payment.Description"/>
        </div>
        <div class="card-title" id="amount">
          <p>$ {{payment.Amount}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ClickToEdit from '@/components/ClickToEdit';

export default {
  name: 'Table',
  components: {
    ClickToEdit
  },
  data() {
    return {
      payments: [],
      sortButtons: [
        {
          filterName: 'ID',
          isActive: false
        },
        {
          filterName: 'Date',
          isActive: false
        },
        {
          filterName: 'Name',
          isActive: false
        },
        {
          filterName: 'Amount',
          isActive: false
        },
      ]
    }
  },
  created: function() {
    // fetches data from excel file on created
    fetch("data.xlsx")
      .then((res) => {
        // get data as blob
        if(!res.ok) throw new Error ("Data fetch failed");
        return res.arrayBuffer();
      })
      .then ((ab) => {
        // parse data
        const data = new Uint8Array(ab);
        const XLSX = require('xlsx');
        const wb = XLSX.read(data, {
          type:"array"
        });
        // store data in JSON array
        const sheet = wb.SheetNames[0];
        const dataSheet = wb.Sheets[sheet];
        this.payments = XLSX.utils.sheet_to_json(dataSheet);
      });
  },

  methods: {
    // re-sorts data from based on selected filter
    sortBy(filter) {
      this.payments.sort((first, second) => {
        if (first[filter] > second[filter] ) {
          return 1
        } else if (first[filter] < second[filter]) {
          return -1
        } else {
          return 0
        }
      })
    },

    // resets filter states then toggles the passed filter to active
    toggleSort(index) {
      this.sortButtons.forEach((button) => {
        button.isActive = false
      });
      this.sortButtons[index].isActive = true;
    }
  }
}
</script>

